@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #B3B3CB ;
;
}

code {
  font-family: 'Roboto Slab', serif;
}

.container{
    
    display:flex;
    flex-direction: column;
}
h1{
    font-family: 'Roboto Slab', serif;
    font-weight: 500;
    margin-top: 1%;
    margin-left: 2%;
}
.dataTable{
    /* background: linear-gradient(45deg, #49a09d, #9a6bb9); */
    width:800px;
    text-align: center;
    align-self: center;
    
}
thead{
		background-color: #9095BD;
        color:white;

}
th{
    font-family: 'Roboto Slab', serif;
    font-weight: 400;
}
tbody{
    background-color: #B6BFE3;
    
}
td {
    position: relative;
    font-family: 'Roboto Slab', serif;
    font-weight: 300;
    color:white;
}

td:hover  td:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: -9999px;
    bottom: -9999px;
    background-color: rgba(255,255,255,0.2);
    z-index: -1;
}

tr:hover {
    background-color: rgba(255,255,255,0.3);
    
}

.dialogTitle{
    font-family: 'Roboto Slab', serif;
    font-weight: 600;
    background-color:#9095BD;
    color:#cee5e7;
}
.editBtn .delBtn{
    font-family: 'Roboto Slab', serif;
    font-weight: 400;

}

.dialog-content{
    font-family: 'Roboto Slab', serif;
    font-weight: 300;
    background-color: #9095BD;
    color:#cee5e7;
    
}
.dialog-action{
    font-family: 'Roboto Slab', serif;
    font-weight: 300;
    background-color:#9095BD;
    color:white;
}
.dialog-input{
    color:white;
}
.dialog-button{
    color:white;
}
.dialog-input:focus{
    color:white
}
